<template>
	<div class="position-relative" style="z-index: 1;">
		<transition name="fade">
		    <tip v-if="!show" :tipMessage="tipMessage"></tip>
		</transition>
		
		 <!-- 富文本编辑器 -->
		<div>
			<input placeholder="文章标题限制50字" type="text" v-model="editor.title" style="height: 50px;width: 100%;"/>
			
			<h4>请选择文章类型(必填)</h4>
			<div style="height: 20px;"></div>
			<select v-model="editor.typeSelect" @change="changeProduct($event)" style="width: 200px;">
				<option value="1">租车</option>
				<option value="2">景点</option>
				<option value="3">课堂</option>
			</select>
			<div style="height: 20px;"></div>
		</div>
		 
		 <quill-editor v-model="editor.goods_introduce"></quill-editor>
		 <button @click="submit()" style="color: #F8F8F8;background-color: #007BFF;width: 100%;height: 50px;font-size: 20px;border-radius: 5px;cursor: pointer;">提交</button>
		 
	</div>
</template>

<script>
	const querystring = require('querystring');
	import tip from "./tip.vue"
	export default{
		components: {
			tip
		},
		data() {
			return {
				
				show: true,
				tipMessage:"提交成功",
				editor: {
					title:"",
					goods_introduce:"",
					typeSelect:""
				}
			}
		},
		mounted() {
			this.$store.commit("show",this.$route.fullPath)
			// console.log(this.editor.typeSelect)
		},
		methods: {
			changeProduct(a){
				this.editor.typeSelect = a.target.value
				
			},
			submit() {
				// console.log(this.editor.typeSelect)
				if(this.editor.title || this.editor.goods_introduce||!this.editor.typeSelect){
					// console.log(this.editor.title.length)
					if(this.editor.title.length > 50){
						alert("标题限制50字")
					}else{
						let that = this
						this.$A.post('/api/editDetail',querystring.stringify({
								"title":that.editor.title,
								"content":that.editor.goods_introduce,
								"time": Date.now(),
								"type":this.editor.typeSelect
								})).then(res =>{
									  console.log(res)
									  this.show=false
									  setTimeout(()=>{
										this.show=true
									  },3000)
									}).catch(error =>{
									console.log(error)
								}
							)
					}
					
				}else{
					alert("标题和内容不能为空")
				}
			}
			// 方法二  不兼容ie
			// submit() {
			// 	let that = this
			// 	let param = new URLSearchParams()
			// 	param.append("title", that.editor.title)
			// 	param.append("content", that.editor.goods_introduce)
			// 	param.append("time", Date.now())
				
			// 	this.$A.post('/api/editDetail',param)
			// 	.then(function(res){
			// 	  alert("提交成功")
			// 	})
			// 	.catch(function(err){
			// 	  console.log(err);
			// 	});
				
			// }
		},
		
	}
</script>

<style>
	.ql-editor{
	  height: 300px;
	}
	.fade-enter-active, .fade-leave-active {
	  transition: opacity .5s;
	}
	.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
	  opacity: 0;
	}
</style>
